import React from 'react'
import Pdf from './files/Charles_Curtis_Resume.pdf'

const Contact = () => {
  return (
    <div className="container">
      <div className="row">
        <div className="resume col-sm-12 col-lg-6">
          <h3>Resume</h3>
          <div>
            <a className="btn btn-lg btn-success" href={Pdf} download="CharlesCurtisResume">View/Download Resume</a>
          </div>
        </div>
        <div className="contact col-sm-12 col-lg-6">
          <h3>Contact</h3>
          <div>
            <a className="mx-2" href="mailto:chuck.curtis.su@gmail.com">
              <img src="https://img.icons8.com/color/96/000000/gmail.png"/>
            </a>
            <a className="mx-2" onClick={() => window.open('https://www.linkedin.com/in/chuck-curtis/', '_blank')}>
              <img src="https://img.icons8.com/color/96/000000/linkedin.png"/>
            </a>
            <a className="mx-2" onClick={() => window.open('https://github.com/cpcurtis1218', '_blank')}>
              <img src="https://img.icons8.com/windows/96/000000/github.png"/>
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Contact



