import React from 'react'
import { Link } from 'react-scroll'
import { Navbar, Nav } from 'react-bootstrap'

const Header = () => (
  <Navbar expand="md" className="sticky-top m-0 header" variant="dark">
    <Link
      to='home'
      spy={true}
      smooth={true}
      offset={-130}
      duration={500}
      className="logo pl-4"
      style={{ textDecoration: 'none' }}
    ><img src="https://i.imgur.com/4ilXfva.png"/></Link>
    <Navbar.Toggle aria-controls="basic-navbar-nav" />
    <Navbar.Collapse id="basic-navbar-nav">
      <Nav className="ml-auto text-center">
        <Link
          to='home'
          spy={true}
          smooth={true}
          offset={-130}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >Home</Link>
        <Link
          to='about'
          spy={true}
          smooth={true}
          offset={-100}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >About</Link>
        <Link
          to='projects'
          spy={true}
          smooth={true}
          offset={-100}
          duration={500}
          className="scroll-nav"
          style={{ textDecoration: 'none' }}
        >Work</Link>
        <a className="scroll-nav" onClick={() => window.open('https://webdevwithchuck.wixsite.com/letsgoapp/blog', '_blank')}>
        Blog
        </a>
      </Nav>
    </Navbar.Collapse>
  </Navbar>
)

export default Header



